	position: absolute;
	cursor: text;
	overflow: hidden;
}

/* -- smooth-caret-animation -- */
.monaco-editor .cursors-layer.cursor-smooth-caret-animation > .cursor {
	-webkit-transition: 80ms;
	transition: 80ms;
}

/* -- block-outline-style -- */
